.header {
  height: 90vh;
  width: calc(100% - 24px);

  background-color: rgba(5, 63, 60, 0.817);

  border-radius: 32px;
  /* border: 20px solid white; */
  margin: 12px;
}

.nav_container {
  max-width: 1500px;
  /* border: 1px solid black; */

  padding: 2.85vw 3.42vw 0px 3.42vw;
  margin: 0 auto;

  user-select: none;
}

.nav_content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;

  cursor: pointer;

  width: fit-content;
}

.logo_content {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.logo_content > * + *:not(.nav_tel__link) {
  margin-left: 12px;
}

.logo > .nav_tel__link {
  margin-left: 0px !important;
  color: white;
  font-size: 28px;
}

.logo img {
  width: 52px;
  height: 52px;
}

.logo p {
  color: #fff;

  font-family: "Onest";
  font-size: 42px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
}

.logo p span {
  color: #f4bc38;
}

.menu {
  display: flex;
  flex-direction: row;
  /* gap: 48px; */
}

.menu > * + * {
  margin-left: 48px;
}

.menu .menu_item {
  position: relative;
}

.menu .menu_item a {
  color: #fff;

  font-family: "Roboto";
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;

  cursor: pointer;

  transition: all 0.25s ease-in-out;

  text-decoration: none;

  white-space: nowrap;
}

.menu .menu_item a:hover {
  color: #ffe7b0;
}

.menu .menu_item::after {
  position: absolute;
  bottom: -6px;
  left: 50%;

  content: "";

  width: 0%;
  height: 2px;
  border-radius: 2px;

  background-color: white;
  transition: all 0.5s ease-in-out;
}

.menu .menu_item.active a {
  color: #f4bc38;
}

.menu .menu_item.active::after {
  background-color: #f4bc38;

  left: 0;
  width: 100%;
}

.nav_tel {
  color: white;

  font-family: "Nunito";
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  text-align: center;
  line-height: 32px;

  text-decoration: none;

