.calculator_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* gap: 1.14vw; */

  margin-top: 2.75vw;

  padding: 1.2vw;

  background-color: #fbfaf7;
  border-radius: 32px;

  /* max-width: 750px; */
  width: 34vw;
  height: fit-content;
  box-sizing: content-box;

  /* outline: 12px solid rgba(176, 240, 250, 0.065); */

  box-shadow: 0px 0px 20px rgb(24, 40, 44);
  z-index: 1;

  border: none;

  position: relative;
}


i[data-tooltip]::after {
  content: attr(data-tooltip);
  
  width: 340px;
  height: fit-content;

  position: absolute;
  right: -20px;
  top: 40px;
  /* bottom: -100%; */
  background-color: white;
  padding: 4px 8px;
  border-radius: 8px;
  /* transform: scale(0); */
  transition: 0.3s ease-in-out;
  

  opacity: 0;


  font-family: "Onest";
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 180%;

  text-align: center;

  color: white;

  padding: 20px 16px;

  display: none;
}

i[data-tooltip]:hover::after {
  /* transform: scale(1); */
  top: 40px;
  right: 0px;
  opacity: 1;
  background-color: rgba(4, 8, 5, 0.92);


  display: block;
}

i[data-tooltip]:hover{
  animation-name: none;
}

i {
  position: absolute;
  top: 20px;
  right: 20px;

  width: 32px;
  height: 32px;


  z-index: 100;

  background-color: rgba(245, 19, 19, 0.783);
  background-image: url('../assets/alert.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  border-radius: 20px;

  cursor: pointer;

  animation-name: animNotify;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-fill-mode: forwards;
}


@keyframes animNotify {
  0%{
    
  background-color: rgba(245, 19, 19, 0.783);
  }
  50%{
    
  background-color: rgba(245, 19, 19, 0.2);
  }
  100%{
    
  background-color: rgba(245, 19, 19, 0.783);
  }
}


.calculator_container::after {
  position: absolute;
  top: -12px;
  left: -12px;

  content: "";

  width: calc(100% + 24px);
  height: calc(100% + 24px);

  border-radius: 42px;
  border: 12px solid rgba(176, 240, 250, 0.065);

  pointer-events: none;
}

.calculator_container > * + * {
  margin-top: 1.14vw;
}

.calculator_container .calculator_title {
  color: rgb(32, 32, 32);

  font-family: "Onest";
  font-size: 1.25vw;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  text-align: center;

  margin: 0;
}

.calculator_description{
  color: #f01e2c;

  font-family: "Onest";
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  text-align: center;

  margin: 10px 0;
}

.calculator_body {
  /* max-width: 750px; */
  width: 100%;
  height: fit-content;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* gap: 0.85vw; */
}

.calculator_body > * + * {
  margin-top: 0.85vw;
}

.calculator_body--inputs {
  display: flex;
  flex-direction: row;
  align-items: center;
  /* gap: 16px; */

  width: 100%;
  height: 100%;
}

.calculator_body--inputs > * + * {
  margin-left: 16px !important;
}

.calculator_body--inputs .map_address {
  width: 100%;
  height: 100%;
}

.calculator_body--inputs .map_address input {
  /* width: 260px; */
  height: calc(2.214vw - (0.607vw * 1.7));

  border-radius: 20px;

  padding: 0.42vw 0.857vw;

  border: 1px solid rgb(158, 158, 158);

  color: #2f2f2ff1;

  font-family: "Onest";
  font-size: 0.65vw;
  font-style: normal;
  font-weight: 500;
  line-height: 100%;
}

input:focus {
  outline: none !important;
}


.calculator_body--inputs input {
  width: calc(100% - 1.7142vw);

  padding: 0.607vw 0.8571vw;
}

.calculator_body--inputs .number-input {
  display: flex;
  width: fit-content !important;

  justify-content: center;
  align-items: center;

  /* gap: 8px; */
}

.calculator_body--inputs .number-input > * + * {
  margin-left: 8px !important;
}

button.inactive {
  pointer-events: none !important;
  opacity: 0.55 !important;
}

.calculator_body--inputs .number-input button {
  width: 2.2vw !important;
  height: 2.2vw !important;
  padding: 0 !important;

  display: block !important;

  /* margin: 0 !important; */

  color: rgb(0, 0, 0);
  text-align: center;
  font-family: Onest;
  font-size: 0.642;
  font-style: normal;
  font-weight: 600;
  line-height: 20px; /* 222.222% */

  border-radius: 20px;

  transition: all 0.3s ease-in-out;

  background-color: #ffbe25;
}

.calculator_body--inputs .number-input button:hover {
  opacity: 0.75;
}

.calculator_body--inputs .number-input input {
  /* margin: 0px !important; */

  display: block;

  height: 2.214vw;

  background-color: white;
  padding: 0.3vw 0.6vw;

  border-radius: 20px;
  border: 1px solid rgb(158, 158, 158);

  color: rgb(86, 86, 86);
  text-align: center;
  font-family: Onest;
  font-size: 0.85vw;
  font-style: normal;
  font-weight: 600;
  line-height: 20px; /* 222.222% */

  width: 2.85vw;
}

button {
  display: block;
  width: fit-content;

  border: none;

  border-radius: 40px;

  padding: 0.85vw 2.75vw;

  color: rgb(0, 0, 0);
  text-align: center;
  font-family: Roboto;
  font-size: 1.2vw;
  font-style: normal;
  font-weight: 600;
  line-height: 20px; /* 222.222% */

  margin: 0 auto;
  margin-top: auto;
  margin-bottom: 1vw;

  background-color: #ffbe25;

  cursor: pointer;
  user-select: none;
}

.calculator_body > button {
  padding: 0.785vw 2.28vw !important;
  font-size: 0.785vw !important;
  color: black;
  margin: 0 auto;
  margin-top: 0.92vw;
  transition: all 0.3s ease-in-out;
}

.calculator_body > button:hover {
  opacity: 0.75;
}

.calculator_body--map {
  width: 100%;
  height: 11.5vw;

  overflow: hidden;

  background-color: #f4bc3814;

  border-radius: 14px;

  animation-name: blindSkeleton;
  animation-duration: 0.8s;
  animation-iteration-count: infinite;
}

@keyframes blindSkeleton {
  0% {
    background-color: #f4bc3814;
  }
  70% {
    background-color: #f4bc3803;
  }
  100% {
    background-color: #f4bc3814;
  }
}

